<script>
  import DemoLinkorg from '../components/demo';
  import Vue from 'vue'
export default {
  name: 'vTip',
  components: {DemoLinkorg},
  methods: {
    render() {
      return new Vue({
        data: { name: 'Home', placement: 'right'},
        render(h) {
          return (
            <div>
              <h1><em style="color: #e4961e">vNode</em></h1>
              <p><a style="color:white" href="https://linkorg.club">{this.name}</a></p>
              <ve-button onClick={this.add} v-tip={{content: 'i am vEasy '+this.placement+' , I am on the '+this.placement+' side of the button', placement: this.placement, offset: 34, enterable: false, Class: 'my-tip'}}>Get timeStamp</ve-button>
            </div>
        )
        },
        methods: {
          add() {
            this.name = new Date().getTime();
            this.placement = ['left', 'right', 'top', 'bottom'][Math.floor(Math.random() * 4)];
          }
        }
      });
    },
  }
}
</script>

