<script>
/* eslint-disable import/extensions */

import DemoLinkorg from '../components/demo';

export default {
  components: {
    DemoLinkorg,
  },
  data() {
    return {
      msg: '',
      color: '#5e6d82',
    };
  },
  methods: {
    send(type) {
      const aIo = this.$msg({
        type,
        message: '',
        duration: 2000,
      });
      setTimeout(aIo.close, 1000);
    },
    sendHidden(type) {
      this.$msg({
        type,
        message: '',
        duration: 2000,
        showClose: false,
      });
    },
    sendOnclose(type) {
      this.msg = '';
      this.color = '#5e6d82';
      this.$msg({
        type,
        message: '',
        duration: 5000,
        onClose: () => {
          this.msg = '';
          this.color = '#f53939';
        },
      });
    },
  },
};
</script>

